.header{
    
    background-image:linear-gradient(to bottom,blue,lightblue, silver,orange ,yellow);
    text-align: center;
}

.header h1{
    font-size: 50px;
}



@media only screen and (width: 768px) {
    .header{
        width: 98%;
        margin:1%;
    }
    .header h1{
        font-size: 20px;
    }
}